import React, { useEffect, useState } from "react";
import { dbService} from "../fbase";
import Chat from "../components/Chat";
import ChatFactory from "../components/ChatFactory";
import ReactHtmlParser from "react-html-parser"
import { Cheerio } from "cheerio";

const Home = ({userObj}) => {
    
    const [chats,setChats]=useState([]);
    
    useEffect( ()=>{
        dbService.collection("chats").orderBy('createdAt','desc').onSnapshot(snapshot=>{
            const chatArray=snapshot.docs.map((doc)=>({
                id:doc.id,
                ...doc.data(),
            }));
            setChats(chatArray);
        });
    },[])
    const Parse= async()=>{
        
        const a= await fetch("http://www.seowon.or.kr/wah/main/schoolmeal/view.htm?menuCode=82&domain.year=2021&domain.month=10&domain.day=26")
        const $=Cheerio.load(a.text());
        console.log($("div.Schoolmeal_Cont_Cont_Cont").children());
    }
return(
    <div className="container">
        <button onClick={Parse} className="formBtn">Button</button>
        <ChatFactory userObj={userObj}/>
        <div style={{marginTop:30}}>
            {chats.map((chat)=>(

                <Chat 
                    key={chat.id} 
                    chatObj={chat} 
                    isOwner={chat.createrId===userObj.uid}/>
                ))}
        </div>
    </div>
    );
};
export default Home;